  margin-top: 1.5em;
  font-family: "Fauna One", sans-serif; }

.quote-container {
  margin-top: 10em; }

.quotation {
  font-size: 1rem;
  quotes: "" "" "" "";
  padding: 10px 20px;
  line-height: 1.4; }

.quotation:before {
  content: open-quote;
  display: inline;
  line-height: 0;
  left: -10px;
  position: relative;
  top: 0.5em;
  color: #aca9bb;
  font-size: 3em; }

.quotation::after {
  content: close-quote;
  display: inline;
  line-height: 0;
  left: 10px;
  position: relative;
  top: 0.5em;
  color: #aca9bb;
  font-size: 3em; }

.links-container {
  margin-top: 1em;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: auto; }
  .links-container li {
    padding: 0 1em; }

.links-icon {
  width: 25px; }

.scroll-container {
  margin-top: 6em; }
  .scroll-container img {
    width: 25px;
    animation: move 1s infinite alternate; }

@keyframes move {
  0% {
    transform: translateY(0px); }
  100% {
    transform: translateY(20px); } }

/* the word animation container */
.cycling-word-container {
  display: flex;
  margin-top: 10em;
  flex-direction: column; }

.cycling-word {
  color: #8685ef;
  position: relative;
  width: 100%; }

.cycling-word span {
  width: 100%;
  position: absolute;
  overflow: hidden;
  right: 0%;
  animation: animate 10s linear infinite;
  opacity: 0; }

.cycling-word span:nth-child(1) {
  animation-delay: 0s; }

.cycling-word span:nth-child(2) {
  animation-delay: 2s; }

.cycling-word span:nth-child(3) {
  animation-delay: 4s; }

.cycling-word span:nth-child(4) {
  animation-delay: 6s; }

.cycling-word span:nth-child(5) {
  animation-delay: 8s; }

@keyframes animate {
  0% {
    opacity: 0;
    transform: translateX(-50px); }
  2% {
    opacity: 1;
    transform: translateX(0px); }
  18% {
    opacity: 1;
    transform: translateX(0px); }
  20% {
    opacity: 0;
    transform: translateX(50px); }
  100% {
    opacity: 0;
    transform: translateX(50px); } }

